import React, { Component } from 'react';
import CatBig from '../layout/loaders/CatBig';

class Gallery extends Component {

    render() {
        const { images } = this.props;
        // if(!(Object.entries(images).length === 0 && images.constructor === Object)){
        if(images.length !== 0){
            let id_num = 0;
            return (
                <div className="gallery-container">
                    {/* {Object.values(images).map(unit => { */}
                    {images.map(unit => {
                        let sizes = this.fixSize(unit);
                        let id_prefix = unit.id+"_"+id_num;
                        id_num++;
                        return <img className="gallery-item" key={id_prefix} src={unit.url} alt="Cat" height={sizes[0]} width={sizes[1]}/>
                    })}
                </div>
            )
        }else{
            return <CatBig/>
        }
    }

    fixSize = (unit) => {
        if(unit.height < 400){
            return [unit.height/2, unit.width/2];
        }else if(unit.height < 500){
            return [unit.height/3, unit.width/3];;
        }else if(unit.height < 600){
            return [unit.height/4, unit.width/4];;
        }else if(unit.height < 800){
            return [unit.height/6, unit.width/6];;
        }else if(unit.height < 1000){
            return [unit.height/8, unit.width/8];;
        }else if(unit.height < 1200){
            return [unit.height/10, unit.width/10];;
        }else{
            return [unit.height/12, unit.width/12];;
        }
    }
}   

export default Gallery;